
.aboutme{
    width: 90%;
    height:max-content;
    margin :0 auto;
    display: flex;
    background: rgb(255, 255, 255);
    flex-direction: column;
    align-items: center;
    color:rgb(75, 75, 75);
    position: relative;
    justify-content: center;
    padding:3rem 0;
    
   @media screen and (max-width:800px){
       height:max-content;
       padding:0 .6rem;
   }
}
.expo{
    display:flex;
    margin-top: 1rem;
    gap: 1.5rem;
    padding:0 1rem;
}

