.image{
    justify-content:center;
    display:flex;
    align-items: center;
    background-color:var(--primary-color);
    width: 80%;
    height: 400px;
    border-radius: 10%;
    img{
        width: 90%;
        aspect-ratio: 1/1;
        object-fit: cover;
    }
}
.aboutheader{
    display:flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
   
}
.aboutheader h3{
    margin-top: 2rem;
    color:rgb(14, 14, 14);
    text-transform: uppercase;
    text-align: center;
    @media screen and (max-width:800px){
        text-align:start;
    }
}
.aboutheader small{
    margin-top: .5rem;
    color:rgb(14, 14, 14);
    text-transform: uppercase;
    text-align: center;
    @media screen and (max-width:800px){
        text-align:start;
    }
}
.aboutme .desc{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 80%;
    place-items: center;
    margin-top: 2rem;
}
.desc svg{
    width: 80%;
    height: 80%;
}

.desc-main{
    display:flex;
    align-items: center;
   
    flex-direction: column;
    height: max-content;
    .interest{
        margin-top: 2rem;
        padding-bottom:.5rem;
        h3{
            margin-bottom:1rem;
            color:black;
        }
        ul{
            padding-left:2rem;
            li{
                list-style: disc;
                margin: .7rem 0;
                font-weight:300;
            }
            
        }
        
    }
    p{ border-left: solid 4px grey;
        padding-left:1rem;
        b{
            font-size:1rem;
        }
        
        font-size:1rem;
        
        @media screen and (max-width:700px) {
            font-size: 1rem;  
        }
    }
}
.desc-main button{
    margin-top: 1rem;
}

@media screen and(max-width:800px) {
    .aboutme .desc{
        display: flex;
        flex-direction: column;
        .image{
            display:none;
        }
    } 

 }