.services-container{
    width: 90%;
    margin: 0 auto;
    height: max-content;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
     @media screen and(max-width:800px) {
            display: flex;
            align-items: center;
            height: max-content;
            margin: 0 auto;
            justify-content: center;
            width: 90%;
     }
      .service-title{
            margin: 4rem 0;
            text-align: center;
        }
    .service-card-container{
        display: flex;

        @media screen and(max-width:800px) {
                display: flex;
                width: 80%;
                flex-direction: column;
                gap: 2rem;
                justify-content: center;
                align-items: center;
                margin-top: 2rem;
               
                
            height:max-content;
        }
        .carddd{
            display:flex;
            height:max-content;
            @media screen and (max-width:800px) {
                display: flex;
                flex-direction: column;
                width: 100%;
                height:max-content
                ;
                justify-content: center;
                align-items: center;
                gap: 2rem;
            }
            .card {
    
                width: 600px;
                height:400px;
                display: flex;
                gap: 1rem;
               
                flex-direction: column;
                justify-content: center;
                align-items: start;
                border-radius: 20px;
                margin-right: 2.5rem;
                transition: all ease-out 0.5s;
                box-shadow: 5px 10px 10px 7px rgba(128,121,121,0.64)
                ;
                @media screen and(max-width:700px) {
                width: 100%;
                height: 350px;
                margin-right: 0;
            }img{
                border-radius: 20px;
                width:100%;
                height:85%;
                border-bottom-right-radius: 0px;
                border-bottom-left-radius: 0px;
                object-fit:cover;
            }
            h4{
            width:100%;
            height:15%;
            text-align:center;
            text-transform: uppercase;
            }
        }
        
      
    }
}




    .service-card-container .card:hover{
        // box-shadow: 5px 10px 10px -7px rgba(11, 197, 172, 0.64)
    
        transform: scale(0.9);
    }

 


}