main{
    color:black;
    width: 90%;
    margin:0 auto;
   height: 100vh;
   background-position: center;
   background-repeat: no-repeat;
   background-size:cover;
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 1.5rem;
  
   @media screen and (max-width:800px){
    
    display: flex;
    flex-direction: column;
    padding-top: 12vh;
    height:max-content;
    margin-bottom:4rem;
           
}
  .center{
      width:50%;
      text-align: center;
      @media screen and (max-width:800px){
        margin-top: 3rem;
    }
      button{
         
          a{
              color:white;
              font-weight: 400;
          }
      }
      h1{
        color:black;
        z-index: 5;
        font-size: 2.5rem;
        margin-bottom: 1rem;
      
      }
      p:last-of-type{
            font-size: 1rem;
          margin-bottom: 2rem;
     }
  } 
  .svg__img__container{
      position: relative;
      width:40%;
      height:70%;
      background: #ffe32e70;
      border-radius:50%;
      img{
          object-fit: cover;
          border-radius: 50%;
          position:absolute;
        top:-2%;
        width:100%;
      right:1%;
        height:100%;
          @media screen and (max-width:800px) {
            width: 80%;
          height: 250px;
          object-fit: cover;
          border-radius: 50%;
          width: 102%;
          height: 137%;
          /* height: 50%; */
          position: absolute;
          top: -95px;
          right: -9px;
          }
          
      }
      @media screen and (max-width:800px) {
        background: #ffe32ed1;
        display: flex;
        /* align-items: flex-end; */
        padding-top: 5rem;
        width: 80%;
        height: 270px;
        justify-content: center;
        align-content: center;
        border-radius: 50%;
        position: relative;
         
      }
  }
 
  

    @media screen and (max-width:800px) {
        
    
     display: flex;
     flex-direction: column;   
     .icons{
         display:none;
    }
    .center{
        width:100%;
        
        h1{
            width:100%;
            font-size:2.2rem;
            margin-bottom: 1.5rem;
        }
        p:first-of-type{
   margin-top: -1rem;
   font-size: 2.1rem;
}
 p:last-of-type{
   z-index: 5;
   color:black;
     margin-bottom: 2rem;
}
    }
    }
   
    
}

main p:first-of-type{
   margin-top: -1rem;
   font-size: 1.6rem;
   margin-bottom: 1rem;
}
main p{
   z-index: 5;
   color:black;
   
}
